import { MpPseudoBox } from '@mekari/pixel-pseudo-box';
import { MpIcon } from '@mekari/pixel-icon';
import { createStyledAttrsMixin } from '@mekari/pixel-utils';

/**
 * Exported tag properties.
 *
 * @prop {string|object} as - Component used for root node.
 * @prop {string|object} to - Link to open when tag is clicked.
 * @prop {string} type - Type of tag.
 * @prop {string} size - Size of tag.
 * @prop {string} variant - Variant of tag.
 * @prop {string} icon - Displayed icon on left.
 * @prop {boolean} isClosable - If true, close icon displayed on right.
 * @prop {string} ariaLabel - Optional prop for accessibility.
 * @prop {string} ariaLabelledBy - Optional prop for accessibility.
 */
const useTagProps = {
  as: {
    type: [String, Object],
    default: 'button'
  },
  to: [String, Object],
  type: {
    type: [String],
    default: 'button'
  },
  size: {
    type: [String],
    default: 'md'
  },
  variant: {
    type: [String],
    default: 'gray'
  },
  icon: [String],
  isClosable: {
    type: [Boolean],
    default: false
  },
  ariaLabel: [String],
  ariaLabelledBy: [String]
};

/**
 * Exported tag styles.
 */
const baseStyles = {
  position: 'relative',
  display: 'inline-flex',
  alignItems: 'center',
  userSelect: 'none',
  outline: '0px solid transparent',
  borderWidth: '1px',
  borderColor: 'transparent',
  borderRadius: 'full',
  transition: 'all 250ms'
};
const sizeStyles = {
  md: {
    fontSize: 'md',
    fontWeight: 'regular',
    lineHeight: 'md',
    letterSpacing: 'normal',
    paddingX: '2',
    paddingY: '1'
  },
  sm: {
    fontSize: 'md',
    fontWeight: 'regular',
    lineHeight: 'md',
    letterSpacing: 'normal',
    paddingX: '2',
    paddingY: '0'
  }
};

const componentStyles = ({
  variant
}) => {
  const variantStyles = {
    blue: {
      color: 'blue.400',
      backgroundColor: 'blue.50',
      _hover: {
        color: 'blue.500'
      },
      _focus: {
        borderWidth: '1px',
        borderColor: 'blue.400',
        boxShadow: 'outer'
      }
    },
    red: {
      color: 'red.400',
      backgroundColor: 'red.50'
    },
    outline: {
      color: 'blue.400',
      backgroundColor: 'white',
      borderWidth: '1px',
      borderColor: 'blue.50',
      _hover: {
        backgroundColor: 'blue.50'
      },
      _focus: {
        borderWidth: '1px',
        borderColor: 'blue.400',
        boxShadow: 'outer'
      }
    },
    gray: {
      color: 'gray.600',
      backgroundColor: 'gray.50'
    },
    ghost: {
      color: 'gray.600',
      backgroundColor: 'white',
      _hover: {
        color: 'dark',
        backgroundColor: 'gray.50'
      },
      _focus: {
        borderWidth: '1px',
        borderColor: 'gray.400',
        boxShadow: 'outer'
      }
    }
  };

  switch (variant) {
    case 'blue':
      return variantStyles['blue'];

    case 'red':
      return variantStyles['red'];

    case 'outline':
      return variantStyles['outline'];

    case 'gray':
      return variantStyles['gray'];

    case 'ghost':
      return variantStyles['ghost'];

    default:
      return variantStyles['blue'];
  }
};

const useTagStyles = props => {
  return { ...baseStyles,
    ...sizeStyles[props.size],
    ...componentStyles(props)
  };
};

/**
 * Offers a label that can optionally be selected and unselected or removed.
 *
 * @category Data entry - Component
 * @see Docs - https://storybook.mekari.design/?path=/docs/components-data-entry-tag
 * @see NPMJS - https://www.npmjs.com/package/@mekari/pixel-tag
 */

const MpTag = {
  name: 'MpTag',
  mixins: [createStyledAttrsMixin('MpTag')],
  props: { ...useTagProps
  },

  data() {
    return {
      isHovered: false
    };
  },

  computed: {
    componentStyles() {
      return useTagStyles({
        size: this.size,
        variant: this.variant
      });
    },

    getGradientBackground() {
      if (this.variant === 'red') {
        return `linear-gradient(270deg, #FDECEE 75%, rgba(237, 240, 242, 0) 111.54%)`;
      }

      return `linear-gradient(270deg, #EDF0F2 75%, rgba(237, 240, 242, 0) 111.54%)`;
    }

  },
  methods: {
    handleClick(e) {
      this.$emit('click', e);
      this.$emit('onClick', e);
    },

    handleClose(e) {
      e.stopPropagation();
      this.$emit('close', e);
      this.$emit('onClose', e);
    },

    handleMouseEnter() {
      this.isHovered = true;
    },

    handleMouseLeave() {
      this.isHovered = false;
    }

  },

  created() {
    if (typeof this.$listeners.onClick !== 'undefined') console.error('[Pixel error]: Deprecated on `onClick` event and use `click` instead.');
    if (typeof this.$listeners.onClose !== 'undefined') console.error('[Pixel error]: Deprecated on `onClose` event and use `close` instead.');

    if (this.variant === 'blue' || this.variant === 'outline' || this.variant === 'ghost') {
      console.error('[Pixel error]: Deprecated on `variant` prop `blue`, `outline`, and `ghost`');
    }

    if (this.icon) {
      console.error('[Pixel error]: Deprecated on `icon` prop');
    }
  },

  render(h) {
    return h(MpPseudoBox, {
      class: this.className,
      props: {
        as: this.as,
        to: this.to
      },
      attrs: {
        tabIndex: '0',
        type: this.type,
        'aria-label': this.ariaLabel,
        'aria-labelledby': this.ariaLabelledBy,
        ...this.computedAttrs
      },
      on: {
        click: this.handleClick,
        mouseenter: this.handleMouseEnter,
        mouseleave: this.handleMouseLeave
      }
    }, [!this.isClosable && this.icon && h(MpPseudoBox, {
      props: {
        as: 'span'
      },
      attrs: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        marginRight: '1'
      },
      on: {
        click: this.handleClose
      }
    }, [h(MpIcon, {
      props: {
        name: this.icon,
        size: 'sm'
      },
      attrs: {
        color: 'currentColor',
        focusable: false
      }
    })]), this.$slots.default, this.isClosable && !this.icon && this.isHovered && h(MpPseudoBox, {
      props: {
        as: this.as,
        to: this.to
      },
      attrs: {
        position: 'absolute',
        right: '0',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        background: this.getGradientBackground,
        height: 'full',
        padding: '2px',
        borderRadius: 'full',
        cursor: 'pointer'
      },
      on: {
        click: this.handleClose
      }
    }, [h(MpIcon, {
      props: {
        name: 'remove-tag',
        size: 'sm'
      },
      attrs: {
        color: 'currentColor',
        focusable: false
      }
    })])]);
  }

};

export { MpTag };
