import { MpInput } from '@mekari/pixel-input';
import { createStyledAttrsMixin } from '@mekari/pixel-utils';

/**
 * Exported textarea properties.
 *
 * @prop {string|object} as - Component used for root node.
 * @prop {number|string} value - Value of textarea.
 * @prop {number|string} defaultValue - Initial value of textarea.
 * @prop {string} placeholder - Placeholder of textarea.
 * @prop {boolean} isReadOnly - If true, textarea be read only.
 * @prop {boolean} isDisabled - If true, textarea will be disabled.
 * @prop {boolean} isInvalid - If true, textarea is marked as invalid.
 * @prop {boolean} isRequired - If true, textarea will be required.
 * @prop {boolean} isFullWidth - If true, textarea will span full width of it parent.
 * @prop {string} ariaLabel - Optional prop for accessibility.
 * @prop {string} ariaLabelledBy - Optional prop for accessibility.
 */
const useTextareaProps = {
  as: {
    type: [String, Object],
    default: 'textarea'
  },
  value: [Number, String],
  defaultValue: [Number, String],
  placeholder: [String],
  isReadOnly: {
    type: [Boolean],
    default: false
  },
  isDisabled: {
    type: [Boolean],
    default: false
  },
  isInvalid: {
    type: [Boolean],
    default: false
  },
  isRequired: {
    type: [Boolean],
    default: false
  },
  isFullWidth: {
    type: [Boolean],
    default: true
  },
  ariaLabel: [String],
  ariaLabelledBy: [String]
};

/**
 * Exported textarea styles.
 */
const componentStyles = ({
  isFullWidth
}) => {
  return {
    position: 'relative',
    display: 'flex',
    alignItems: 'center',
    outline: '0',
    borderWidth: '1px',
    borderColor: 'gray.100',
    borderRadius: 'md',
    color: 'dark',
    backgroundColor: 'white',
    appearance: 'none',
    transition: 'all 250ms',
    width: isFullWidth ? '100%' : undefined,
    minWidth: '65',
    minHeight: '20',
    fontSize: 'md',
    fontWeight: 'regular',
    lineHeight: 'md',
    paddingX: '3',
    paddingTop: '2',
    paddingBottom: '5',
    resize: 'both',
    _hover: {
      borderColor: 'gray.400'
    },
    _focus: {
      borderColor: 'blue.500',
      boxShadow: 'outer'
    },
    _disabled: {
      color: 'gray.400',
      backgroundColor: 'gray.50',
      borderColor: 'gray.100',
      cursor: 'not-allowed'
    },
    _invalid: {
      borderColor: 'red.400'
    },
    _readOnly: {
      boxShadow: 'none',
      userSelect: 'all'
    }
  };
};

const useTextareaStyles = props => {
  return { ...componentStyles(props)
  };
};

/**
 * Enables users to add longer text to a form.
 *
 * @category Data entry - Component
 * @see Docs - https://storybook.mekari.design/?path=/docs/components-data-entry-textarea
 * @see NPMJS - https://www.npmjs.com/package/@mekari/pixel-textarea
 */

const MpTextarea = {
  name: 'MpTextarea',
  mixins: [createStyledAttrsMixin('MpTextarea')],
  props: { ...useTextareaProps
  },
  computed: {
    getTextareaStyles() {
      return useTextareaStyles({
        isFullWidth: this.isFullWidth
      });
    }

  },

  render(h) {
    return h(MpInput, {
      props: { ...this.$props,
        as: 'textarea',
        variant: 'unstyled',
        isClearable: false
      },
      attrs: { ...this.getTextareaStyles,
        ...this.splitProps.styleAttrs,
        ...this.computedAttrs
      },
      on: this.$listeners
    }, this.$slots.default);
  }

};

export { MpTextarea };
