          const isDisableSuggestion = this.getFlattenSuggestions.filter(item => {
            return item[this.suggestionKey] === this.getFilteredSuggestions[0] && item.isDisabled;
          }).length !== 0;

          if (isDisableSuggestion) {
            return;
          }
