import { MpBox } from '@mekari/pixel-box';
import { MpControlBox } from '@mekari/pixel-control-box';
import { MpIcon } from '@mekari/pixel-icon';
import { MpVisuallyHidden } from '@mekari/pixel-visually-hidden';
import { createStyledAttrsMixin, useId, isDef, cleanChildren, cloneVNodeElement } from '@mekari/pixel-utils';

/**
 * Exported checkbox properties.
 *
 * @prop {string} id - Id of checkbox.
 * @prop {string} name - Name of checkbox.
 * @prop {string|number|boolean} value - Value of checkbox.
 * @prop {boolean} isChecked - If true, checkbox will be checked.
 * @prop {boolean} defaultIsChecked - If true, checkbox will be initially checked.
 * @prop {boolean} isIndeterminate - If true, checkbox will be partially checked.
 * @prop {boolean} isReadOnly - If true, checkbox be read only.
 * @prop {boolean} isDisabled - If true, checkbox will be disabled.
 * @prop {boolean} isInvalid - If true, checkbox is marked as invalid.
 * @prop {string} ariaLabel - Optional prop for accessibility.
 * @prop {string} ariaLabelledBy - Optional prop for accessibility.
 */
const useCheckboxProps = {
  id: [String],
  name: [String],
  value: [String, Number, Boolean],
  isChecked: {
    type: [Boolean],
    default: false
  },
  defaultIsChecked: {
    type: [Boolean],
    default: false
  },
  isIndeterminate: {
    type: [Boolean],
    default: false
  },
  isReadOnly: {
    type: [Boolean],
    default: false
  },
  isDisabled: {
    type: [Boolean],
    default: false
  },
  isInvalid: {
    type: [Boolean],
    default: false
  },
  ariaLabel: [String],
  ariaLabelledBy: [String]
};

/**
 * Exported checkbox styles.
 */
const baseStyles = {
  position: 'relative',
  outline: '0px solid transparent',
  borderWidth: '1.5px',
  borderColor: 'inherit',
  borderRadius: 'sm',
  userSelect: 'none',
  transition: 'border-color 250ms, outline-color 250ms, background-color 120ms, box-shadow 250ms'
};

const componentStyles = ({
  isChecked,
  isReadOnly,
  isInvalid
}) => {
  const setReadOnlyStyle = styles => {
    return isReadOnly ? isChecked ? styles[0] : styles[1] : styles[2];
  };

  return {
    color: isReadOnly && 'white',
    backgroundColor: setReadOnlyStyle(['blue.400', 'white', 'white']),
    borderColor: setReadOnlyStyle(['blue.400', 'gray.100', 'gray.100']),
    _hover: {
      borderColor: setReadOnlyStyle(['blue.400', 'gray.400', isInvalid ? 'red.400' : 'gray.400'])
    },
    _checked: {
      color: !isReadOnly && 'white',
      backgroundColor: setReadOnlyStyle(['blue.400', 'white', 'blue.400']),
      borderColor: setReadOnlyStyle(['blue.400', 'gray.100', 'blue.400'])
    },
    _disabled: {
      backgroundColor: 'gray.50',
      borderColor: 'gray.100'
    },
    _focus: {
      boxShadow: 'outer'
    },
    _invalid: {
      borderColor: 'red.400'
    },
    _checkedAndHover: {
      backgroundColor: setReadOnlyStyle(['blue.400', 'white', 'blue.500']),
      borderColor: setReadOnlyStyle(['blue.400', 'gray.400', 'blue.500'])
    },
    _checkedAndDisabled: {
      color: 'gray.50',
      backgroundColor: 'gray.400',
      borderColor: 'gray.400'
    }
  };
};

const useCheckboxStyles = props => {
  return { ...baseStyles,
    ...componentStyles(props)
  };
};

/**
 * A checkbox allows user to toggle between checked, unchecked, and indeterminate states.
 *
 * @category Data entry - Component
 * @see Docs - https://storybook.mekari.design/?path=/docs/components-data-entry-checkbox
 * @see NPMJS - https://www.npmjs.com/package/@mekari/pixel-checkbox
 */

const MpCheckbox = {
  name: 'MpCheckbox',
  mixins: [createStyledAttrsMixin('MpCheckbox')],
  inject: {
    $useFormControl: {
      default: null
    }
  },
  model: {
    prop: 'isChecked',
    event: 'change'
  },
  props: { ...useCheckboxProps
  },
  computed: {
    getFormControls() {
      if (!this.$useFormControl) {
        return {
          isReadOnly: this.isReadOnly,
          isDisabled: this.isDisabled,
          isInvalid: this.isInvalid
        };
      }

      return this.$useFormControl(this.$props);
    },

    getCheckboxStyles() {
      return useCheckboxStyles({
        isChecked: this.isChecked || this.defaultIsChecked,
        isReadOnly: this.getFormControls.isReadOnly,
        isInvalid: this.getFormControls.isInvalid
      });
    },

    getId() {
      return this.id || `checkbox-${useId(4)}`;
    }

  },
  methods: {
    handleChange(e) {
      if (this.getFormControls.isReadOnly) return false;
      this.$emit('change', !this.isChecked, e);
    }

  },

  render(h) {
    return h(MpBox, {
      class: this.className,
      props: {
        as: 'label'
      },
      attrs: {
        for: this.getId,
        display: 'inline-flex',
        minHeight: '7',
        verticalAlign: 'top',
        alignItems: 'center',
        cursor: this.getFormControls.isDisabled ? 'not-allowed' : 'pointer',
        ...this.computedAttrs
      }
    }, [h(MpVisuallyHidden, {
      props: {
        as: 'input'
      },
      domProps: {
        value: this.value,
        defaultChecked: this.getFormControls.isReadOnly ? undefined : this.defaultIsChecked,
        checked: this.getFormControls.isReadOnly ? this.isChecked : this.defaultIsChecked ? undefined : this.isChecked
      },
      attrs: {
        type: 'checkbox',
        id: this.getId,
        name: this.name,
        readOnly: this.getFormControls.isReadOnly,
        disabled: this.getFormControls.isDisabled,
        'aria-readonly': this.getFormControls.isReadOnly,
        'aria-disabled': this.getFormControls.isDisabled,
        'aria-checked': this.getFormControls.isReadOnly ? false : this.isIndeterminate ? 'mixed' : this.isChecked,
        'aria-invalid': this.getFormControls.isInvalid,
        'aria-label': this.ariaLabel,
        'aria-labelledby': this.ariaLabelledBy
      },
      on: {
        change: this.handleChange
      }
    }), h(MpControlBox, {
      attrs: {
        size: '4',
        ...this.getCheckboxStyles
      }
    }, [h(MpIcon, {
      props: {
        name: this.isIndeterminate ? 'checkbox-partial' : 'checkbox-checklist',
        size: 'xs',
        color: 'currentColor'
      },
      attrs: {
        position: 'absolute',
        top: '65%',
        left: '65%',
        transform: 'translate(-50%, -50%)',
        _readOnly: {
          opacity: '1'
        },
        'data-readonly': this.getFormControls.isReadOnly
      }
    })]), this.$slots.default && h(MpBox, {
      props: {
        as: 'span'
      },
      attrs: {
        color: this.getFormControls.isDisabled ? 'gray.400' : 'dark',
        fontSize: 'md',
        fontFamily: 'body',
        lineHeight: 'md',
        minHeight: '7',
        marginLeft: '2',
        paddingTop: '1',
        paddingBottom: '1',
        userSelect: 'none'
      }
    }, this.$slots.default)]);
  }

};

/**
 * Exported checkbox group properties.
 *
 * @prop {string} name - Name of checkbox group.
 * @prop {array} value - Value of checkbox group.
 * @prop {array} defaultValue - Initial value of checkbox group.
 * @prop {boolean} isInline - If true, checkboxes will aligned horizontally.
 * @prop {number|string} spacing - Space between each checkbox.
 */
const useCheckboxGroupProps = {
  name: [String],
  value: [Array],
  defaultValue: [Array],
  isInline: {
    type: [Boolean],
    default: false
  },
  spacing: {
    type: [Number, String],
    default: 2
  }
};

/**
 * A checkbox group allows user to group a related collection of checkboxes in one visual region.
 *
 * @category Data entry - Component
 * @see Docs - https://storybook.mekari.design/?path=/docs/components-data-entry-checkbox
 * @see NPMJS - https://www.npmjs.com/package/@mekari/pixel-checkbox
 */

const MpCheckboxGroup = {
  name: 'MpCheckboxGroup',
  mixins: [createStyledAttrsMixin('MpCheckboxGroup')],
  inject: {
    $useFormControl: {
      default: null
    }
  },
  model: {
    prop: 'value',
    event: 'change'
  },
  props: { ...useCheckboxGroupProps
  },

  data() {
    return {
      values: this.defaultValue || []
    };
  },

  computed: {
    getFormControls() {
      if (!this.$useFormControl) {
        return {
          controlId: this.controlId
        };
      }

      return this.$useFormControl(this.$props);
    },

    isControlled() {
      return isDef(this.value);
    },

    getValues() {
      return this.isControlled ? this.value : this.values;
    },

    getName() {
      return this.name || `checkbox-group-${useId()}`;
    }

  },
  methods: {
    handleChange(_, e) {
      let assignValue;

      if (e.target.checked) {
        assignValue = [...this.getValues, e.target.value];
      } else {
        assignValue = this.getValues.filter(value => value !== e.target.value);
      }

      if (!this.isControlled) {
        this.values = assignValue;
      }

      this.$emit('change', assignValue);
    }

  },

  render(h) {
    const controlId = this.getFormControls.controlId;
    const children = cleanChildren(this.$slots.default);
    const cloneAllElement = children.map((node, index) => {
      const isLastElement = children.length === index + 1;
      const spacingElement = this.isInline ? {
        marginRight: this.spacing
      } : {
        marginBottom: this.spacing
      };
      const cloneElement = cloneVNodeElement(node, {
        props: {
          name: `${this.getName}-${index}`,
          isChecked: this.getValues.includes(node.componentOptions.propsData.value)
        },
        on: {
          change: this.handleChange
        }
      }, h);
      return h(MpBox, {
        attrs: {
          display: this.isInline ? 'inline-block' : 'block',
          ...(!isLastElement && spacingElement)
        }
      }, [cloneElement]);
    });
    return h(MpBox, {
      class: [this.className],
      attrs: {
        role: 'group',
        id: controlId,
        'aria-label': this.ariaLabel || controlId && `${controlId}-control-label`,
        'aria-labelledby': this.ariaLabelledBy || controlId && `${controlId}-help-text`,
        ...this.computedAttrs
      }
    }, cloneAllElement);
  }

};

export { MpCheckbox, MpCheckboxGroup };
